export default "import React from 'react';\r\nimport Icon from './Icon';\r\n\r\nconst IconExample = () => {\r\n  return (\r\n    <div>\r\n      <div>\r\n        <span>这个组件有一个必须的参数【name】就是 svg 的名字.</span>\r\n        <span>也支持点击、鼠标、悬浮等各种事件，默认高宽 [1.4em]。</span>\r\n      </div>\r\n      <div className='ex-icon'>\r\n        <Icon name='settings' />\r\n        <Icon name='close' />\r\n        <Icon name='loading' />\r\n        <Icon name='thumbs-up' />\r\n\r\n      </div>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default IconExample;\r\n"